.image {
	position: relative;
	width: 400px;
}

.image__img {
	display: block;
	width: 100%;
}

.image__overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.6);
	color: #ffffff;
	font-family: 'Quicksand', sans-serif;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	opacity: 0;
	transition: opacity 0.25s;
  border-top: red solid 7px;
}

.image__overlay--blur {
	backdrop-filter: blur(5px);
}

.image__overlay--primary {
  opacity: 0;;
}

.image__overlay>* {
	transform: translateY(20px);
	transition: transform 0.25s;
}

.image__overlay:hover {
	opacity: 1;
}

.image__overlay:hover>* {
	transform: translateY(0);
}

.image__title {
  padding: 5%;
	font-size: 2em;
	font-weight: bold;
}

.image__description {
	font-size: 0.90em;
	margin-top: 0.25em;
